}

.rounded_top_3{
  border-radius: 3px 3px 0 0;
}

.floated_modal_title:hover{
  cursor: all-scroll;
}

.over_x_hidden{
  overflow-x: hidden !important;
}

.accordion-button::after {
  width: 0.8rem !important;
  height: 0.8rem !important;
  background-size: 0.8rem !important;
  margin-right: 5px !important;
}
.main_content_container {
  background: white;
  position: fixed;
  width: calc(100% - 50px);
  height: calc(100% - 50px);
  bottom: 0;
  left: 0;
}
#top_navbar {
  background: linear-gradient(45deg, #1a237e, #8c9eff);
  position: fixed;
  width: calc(100% - 50px);
  height: 50px;
  top: 0;
  left: 0;
  box-shadow: 0 0 9px black;
}
#top_navbar .dropdown-toggle::after {
  color: white;
}
#side_menu {
  background: linear-gradient(45deg, #1a237e, #8c9eff);
  position: fixed;
  width: 50px;
  height: 100vh;
  top: 0;
  right: 0;
  box-shadow: 0 0 9px black;
}

.top_logo_box img {
  filter: drop-shadow(0px 0px 4px white);
  -webkit-filter: drop-shadow(0px 0px 4px white);
  transition: all 200ms ease;
}

.top_logo_box img:hover {
  filter: drop-shadow(0px 0px 8px white);
  -webkit-filter: drop-shadow(0px 0px 8px white);
}

.side_menu_items {
  border-right: 4px solid #eee89700;
  transition: all 300ms ease;
}

.side_menu_items:hover {
  border-right: 4px solid #eee897;
}

.side_menu_items.active {
  border-right: 4px solid #06b370;
}

.menu_items_content {
  position: fixed;
  width: 220px;
  height: calc(100vh - 50px);
  right: -220px;
  top: 50px;
  box-shadow: 0 0 0 #918a8a00;
  transition: all 300ms ease;
  background: linear-gradient(#f4ecf4f7, #fffffff2, #94dbc1c2),
    url(./images/sidebarBack2.svg) no-repeat;
  background-size: cover;
  background-position: center;
  /* background-attachment: fixed; */
}

.menu_items_content.active {
  right: 50px;
  box-shadow: -3px 2px 9px #918a8a;
}

ul {
  list-style-type: none !important;
}

li.disabled {
  opacity: 0.5 !important;
  pointer-events: none !important;
}

#welcome_section {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* floated modal ==========start=====>>========= */
.floated_modal {
  position: fixed;
  /* height: 300px; */
  /* min-width: 500px;
  min-height: 200px; */
  max-height: calc(100vh - 55px);
  z-index: 10000;
  background: #ffffff;
  /* left: 4px;
  top: 53px; */
  box-shadow: 0 0 10px grey;
  border-radius: 3px;
}

.floated_modal .floated_modal_action_icons {
  position: absolute;
  top: 6px;
  left: 5px;
}

.floated_modal .floated_modal_action_icons .floated_modal_pan_icon:hover {
  cursor: move !important;
}

.floated_modal .floated_modal_resize_icon {
  position: absolute;
  bottom: -25px;
  right: -12px;
  transform: rotate(-45deg);
  color: rgb(201, 193, 193);
}

.floated_modal .floated_modal_resize_icon:hover {
  color: rgb(71, 70, 70);
  cursor: nw-resize !important;
}

.floated_modal .floated_modal_content {
  height: calc(100% - 27px) !important;
}
/* floated modal ==========end=====<<========= */

.accordion-button:not(.collapsed) {
  border: 1px solid #6776d591 !important;
}

.accordion-collapse.collapse {
  border-radius: 5px;
  box-shadow: 0 0 5px #6776d591 !important;
}

/* esri lements --------------------------------- */
.esri-component.esri-popup.esri-popup--aligned-top-center.esri-popup--shadow {
  direction: ltr !important;
}
.esri-popup__content {
  direction: rtl !important;
}

.esri-component.esri-attribution.esri-widget {
  display: none !important;
}

.esri-feature__last-edited-info.esri-feature__content-element {
  display: none !important;
}

.esri-popup__header {
  direction: ltr !important;
}

#serch {
  box-shadow: 0 0 5px #aea9a9;
  border-radius: 0.2rem !important;
}
.esri-search {
  width: 240px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  overflow: visible;
}
.esri-widget {
  box-sizing: border-box;
  color: #323232;
  font-size: 14px;
  font-family: "Avenir Next", "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 1.3em;
  background-color: #fff;
  --esri-widget-padding-v: 12px;
