}
.esri-search__container {
  position: relative;
  align-items: stretch;
  flex: 1 0 100%;
}

.esri-popup__navigation{
  direction: ltr !important;
}

.esri-widget__heading.esri-popup__header-title{
  margin: 0 !important;
}

