}

#coordinate_box .change_coordinate_switch_box {
  right: 0 !important;
}

/* corner logo loading ---------------------- */

.bottom_logo_box {
  position: fixed;
  bottom: 5px;
  left: 5px;
  width: 85px;
  text-align: center;
  z-index: 999;
  font-family: "iransans";
  direction: rtl;
  line-height: 13px;
}
.bottom_logo_box img {
  width: 50%;
}
.bottom_logo_box p {
  font-size: 0.6rem;
  text-align: center;
  padding: 0;
  margin: 0;
  color: #01378f;
  font-weight: bold;
}
#circleLoader {
  background: rgb(6, 74, 202);
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  bottom: 26px;
  margin: auto;
  border-radius: 50%;
  animation-name: logo_loader_circle;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes logo_loader_circle {
  0% {
    opacity: 0.9;
    width: 10px;
    height: 10px;
  }
  50% {
    opacity: 0.4;
    width: 60px;
    height: 60px;
  }
  100% {
    opacity: 0;
    width: 90px;
    height: 90px;
  }
}


